@import '../../styles/Shared.scss';

.quote {
    background-color: #162b2b;
    border-radius: 6px;
    padding: 3rem;
    margin: 3rem auto;
    width: 90%;
    max-width: 40rem;
    animation: fade-in 125ms linear;
    box-shadow: $box-shadow;
    
    p {
        font-size: 2.5rem;
    }

    figcaption {
        font-style: italic;
        font-size: 1.5rem;
        text-align: right;
        color: #a1e0e0;
    }
}